
import { defineComponent, ref } from "vue";
import listValRef from "@/core/data/valeur-ref";
import axios from "axios";
interface Filter {
  status: string;
  author: boolean;
  customer: boolean;
  notifications: boolean;
}

export default defineComponent({
  name: "filterAE2",
  props: {
    data_filter: {
      type: Array,
    },
  },
  data: () => ({
    dates: ["2019-09-10", "2019-09-20"],
    listValRef: listValRef,
    filter_options: {
      year: null,
      activite: null,
      service: null,
      zone: null,
      poste_travail: null,
      aspect: null,
      impact: null,
      theme: null,
      situation: null,
      F_G_MAX: null,
      F_G_MIN: null,
      F_G: [0, 99],
      impact_residuel: [0, 99],
      moyen_maitrise_humains: [],
      s_ns: null,
    },
  }),
  components: {},
  computed: {
    currentRouteName() {
      return window.location.origin;
    },
  },
  mounted() {
    this.fetch();
    this.$emit("update:data_filter", this.filter_options);
  },
  methods: {
    resetForm() {
      this.filter_options = {
        year: null,
        activite: null,
        service: null,
        zone: null,
        poste_travail: null,
        aspect: null,
        impact: null,
        theme: null,
        F_G_MAX: null,
        F_G_MIN: null,
        F_G: [0, 99],
        impact_residuel: [0, 99],
        situation: null,
        moyen_maitrise_humains: [],
        s_ns: null,
      };
      this.$emit("update:data_filter", this.filter_options);
    },
    filter() {
      this.$emit("update:data_filter", this.filter_options);
    },
    async fetch() {
      const responseValRef = await axios.get(
        this.currentRouteName + "/api/valeur-ref"
      );
      this.listValRef = responseValRef.data;
    },
  },
  setup() {
    const data = ref<Filter>({
      status: "1",
      author: true,
      customer: true,
      notifications: true,
    });

    return {
      data,
    };
  },
});
